*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  object-fit: contain;
}

body {
  color: @black;
  background-color: @background-gray-first;
  min-height: 100%;
  margin: 0;

  @media (min-width: @tablet-width) {
    .page__body {
      width: 768px;
    }
    };

  @media (min-width: @desktop-width) {
    .page__body {
       width: 1200px;
     }
}
}

.page__body {
  margin: 0 auto;
}
